.popover-moveup-enter-active, .movedown-enter-active {
	transition: all 0.2s ease;
}
.popover-movedown-enter, .movedown-leave-to {
	transform: translateY(-10px);
	opacity: 0;
}
.popover-moveup-enter, .moveup-leave-to {
	transform: translateY(10px);
	opacity: 0;
}
.popover-scale-enter-active {
	transition: all 0.2s ease;
}
.popover-scale-enter, .scale-leave-to {
	transform: scale(0);
	opacity: 0;
}



.editable-list-slide-enter-active, .editable-list-slide-leave-active {
	transition: all 0.5s;
}
.editable-list-slide-enter, .editable-list-slide-leave-to {
	// opacity: 0;
	transform: translateX(100%);
}


