.checkbox, .radio {
	display: inline-block;
	vertical-align: top;
	cursor: pointer;
	transition: all .25s;
	height: 20px;
	position: relative;
	&:hover { color: #FF6000; }
	>span { display: inline-block; float: left; margin-left: 25px; }
	&:not(.checkbox) input+i { border-radius: 50%; }
	input {
		display: none;
		+i {
			margin-right: 6px;
			width: 18px;
			height: 18px;
			color: white;
			background-color: #fff;
			transition: all 0.25s;
			@include border();
			@extend .v-center;
			&:before { @extend .h-v-center; }
		}
		&[type=checkbox]:checked, &[type="radio"]:checked {
			+i { background: #FF6000; border-color: #FF6000; }
		}
	}
	&.on input+i { background: #FF6000; border-color: #FF6000; }
}




/*chklst*/
.checkbox-group, .radio-group {
	width: auto;
	display: inline-block;
	>label { margin-right: 20px; transition: all 0.25s; }
	// 与 couple组合使用多行时
	&.multiple { >label { margin: 5px 20px 5px 0;} }
	&.v { >label { width: 100%; }}
}


.select-ctr {
	position: relative;
	display: inline-block;
	float: left;
	@extend .clear;
	.itemlist { width: 100%; }
	.popover-content.popover-up-arrow { margin-top: 13px;}
	.animated-icon-container {
		@extend .v-center;
		right: 5px;
		svg {
			@extend .h-v-center;
			width: 14px;
			height: 14px;
			stroke-width: 10px;
			stroke: $gray;
			fill: none;
			#up-arrow { display: none; }
		}
	}
}


.u-input {
	position: relative;
	float: left;
	width: 100%;
	border-radius: 3px;
	transition: border-color .25s;
	&:hover {  input::-webkit-input-placeholder { opacity: .6; } }
	input {
		position: relative;
		-webkit-appearance: none;
		padding: 6px 8px;
		width: 100%;
		&::-webkit-input-placeholder { opacity: 0; transition: opacity .25s; }
		border-radius: 3px;
		&:focus { @include shadow-border(#C1E0FE, 3px); border-color: #5EABFF !important; }
		@include border();
	}
	.title { @extend .v-center; left: 8px; color: $font-light; }
	.suffix { @extend .v-center; right: 8px; span { display: inline-block; float: left; } }
	.clear-button { 
        width: 16px;
		height: 16px;
		border-radius: 50%;
		background: #fff;
		right: 8px;
        color: $font-light;
		cursor: pointer;
		transition: all .25s;
		@extend .v-center; 
		svg { 
			width: 12px;
			height: 12px;
			stroke-width: 4px; 
			stroke: currentColor;
			@extend .h-v-center;
		}
		&:hover { color: $warning; }
	}

	.tip { 
		position: absolute; 
		top: -9px; 
		@extend .h-center;
		padding: 0 6px; 
		background: $dark-gray; 
		font-size: 11px; 
		line-height: 16px; 
		color: #fff;
		border-radius: 8px;
		@extend .light-shadow;
	}
}




/*switch-ctr*/
.switch-ctr {
	.switch {
		position: relative;
		display: inline-block;
		vertical-align: top;
		height: 20px;
		width: 35px;
		line-height: 20px;
		transition: all 0.25s;
		cursor: pointer;
		background: #DCDFE4;
		border-radius: 20px;
		transform-origin: 0 50% 0;
		&:before {
			content: '';
			position: absolute;
			display: inline-block;
			width: 18px;
			height: 18px;
			left: 2px;
			border-radius: 50%;
			top: 1px;
			background: white;
			box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.2);
			transition: all 0.25s;
		}
		&.on {
			background: #4BD663;
			&::before {
				left: 16px;
			}

		}
	}
	.description { color: $font-light; @extend .left-distance; }
}




