$unit: 10px;
$dark: #333;
$dark-gray: #535254;
$gray: #777;
$light: #dee0e5;
$feather: #f1f1f1;

$fbBlue: #3b589a;
$pink: #ff3d67;
$green: #6FCF00;
$orange: #ff6000;
$blue: #3388FF;
$olive: #417505;
$transparent: rgba(#fff, 0);

$warning: $orange;
$primary: $fbBlue;
$complement: $pink;
$font-dark: $dark;
$font-light: $gray;
$line-dark: $dark;
$line-light: $light;




.color-dark { color: $dark !important;};
.color-light { color: $light !important;};
.color-primary { color: $primary !important; };
.color-warning { color: $warning !important; };
.color-complement { color: $complement !important; }
.background-primary { background: $primary !important; }
.background-warning { background: $warning !important; }
.background-complement { background: $complement !important; }


.bold { font-weight: bold;}
.light-shadow { box-shadow: 0 2px 7px 0 rgba(0, 0, 0, 0.3); }
.ellipsis { overflow: hidden; text-overflow: ellipsis; white-space: nowrap; }
.auto-width { width: auto-width; }
.auto-height { height: auto-height; }
.zebra-stripe:nth-of-type(2n+1) { background: $feather; } 
.secondary { font-size: 12px; color: $font-light; }
.ctr-core { width: 100%; padding: 10px 8px; border: none; }









.required:after { content: "*"; color: red; position: absolute; padding-left: 3px; font-size: 20px; top: 4px; } 
span.text { display: inline-block; }


.clickable { color: $primary; cursor: pointer; &:hover { text-decoration: underline;} }
.clear:after { content: " "; display: block; height: 0; overflow: hidden; visibility: hidden; clear: both; }

.right-distance { margin-right: $unit !important; &.large-distance { margin-right: $unit*2.5 !important; }}
.left-distance { margin-left: $unit !important; &.large-distance { margin-left: $unit*2.5 !important;} }
.top-distance { margin-top: $unit !important; }
.bottom-distance { padding-bottom: $unit !important; }



.left-side { float: left; @extend .right-distance; }
.leftest-side { float: left; @extend .left-distance;}
.rightest-side { float: right; @extend .right-distance;}
.right-side { float: right; @extend .left-distance; margin-right: 0 !important; }

.h-center { display: inline-block; position: absolute; left: 50%; transform: translatex(-50%); }
.v-center { display: inline-block; position: absolute; top: 50%; transform: translatey(-50%); }
.h-v-center { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%)}

.wrap-20 { padding-top: $unit; padding-bottom: $unit; }
.wrap-14 { padding-top: 7px; padding-bottom: 7px; }
.wrap-10 { padding-top: $unit/2; padding-bottom: $unit/2; }
.wrap-4 { padding-top: 2px; padding-bottom: 2px; }
.unwraped { padding-top: 0 !important; padding-bottom: 0 !important; }

.coat-20 { margin-top: $unit; margin-bottom: $unit; }
.coat-10 { margin-top: $unit/2; margin-bottom: $unit/2; }
.coat-16 { margin-top: 8px !important; margin-bottom: 8px !important; }
.coat-14 { margin-top: 7px; margin-bottom: 7px; }
.coat-8 { margin-top: 4px !important; margin-bottom: 4px !important; }
.coat-6 { margin-top: 3px !important; margin-bottom: 3px !important; }
.coat-4 { margin-top: 2px !important; margin-bottom: 2px !important; }
.coat-2 { margin-top: 1px !important; margin-bottom: 1px !important; }

@mixin border-left($color: $line-light, $strokeWidth: 1px) { border-left: $strokeWidth solid $color }
@mixin border-top($color: $line-light, $strokeWidth: 1px) { border-top: $strokeWidth solid $color }
@mixin border-right($color: $line-light, $strokeWidth: 1px) { border-right: $strokeWidth solid $color }
@mixin border-bottom($color: $line-light, $strokeWidth: 1px) { border-bottom: $strokeWidth solid $color }
@mixin border($color: $line-light, $strokeWidth: 1px) { border: $strokeWidth solid $color}
@mixin border-top-bottom($color: $line-light, $strokeWidth: 1px) { border-bottom: $strokeWidth solid $color; border-top: $strokeWidth solid $color }
@mixin border-left-top($color: $line-light, $strokeWidth: 1px) { border-left: $strokeWidth solid $color; border-top: $strokeWidth solid $color }
@mixin border-right-bottom($color: $line-light, $strokeWidth: 1px) { border-right: $strokeWidth solid $color; border-bottom: $strokeWidth solid $color }
@mixin border-left-bottom($color: $line-light, $strokeWidth: 1px) { border-bottom: $strokeWidth solid $color; border-left: $strokeWidth solid $color }
@mixin hoverable($color) { border-bottom: 1px dotted currentColor; color: $color; cursor: help; }
@mixin shadow-circle-icon($fontSize: 14px) { 
	&:before { 
		background: #fff; 
		border-radius: 50%; 
		@extend .light-shadow; 
		color: $dark-gray; 
		font-size: $fontSize; 
		transition: color .25s;
	}
	&:hover:before { color: #98c0ee; }
}

@mixin inset-shadow-border($color: $line-dark, $strokeWidth: 1px) { box-shadow: 0 0 0 $strokeWidth $color inset; }
@mixin shadow-border($color: $line-dark, $strokeWidth: 1px) { box-shadow: 0 0 0 $strokeWidth $color; }
@mixin starting-price($current: "cny", $fontSize: 30px, $color: $warning) { 
	font-size: $fontSize; 
	color: $color; 
	font-weight: bold;
	font-family: -apple-system !important; 
	&:before { 
		content: attr(data-current);
		font-size: 12px;  
		font-weight: normal;
		margin-right: 2px;
	}
	&:after { 
		content: "起"; 
		font-size: 12px;
		font-weight: normal;
		margin-left: 2px;
	}
	@extend .current-#{$current};
}

.current-cny:before {
	content: "CNY"
}


@mixin tag-icon($name) {
	@extend .icon-#{$name};
	font-family:"iconfont" !important;
	font-style:normal;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	@extend .v-center;
	&:after, &:before {
		font-size: 10px;
	}
}


.fade-enter-active, .fade-leave-active {
	transition: all .3s
}
.fade-enter, .fade-leave-to { 
	opacity: 0;
}

.item-group {
	@extend .clear;
	display: inline-block;
  vertical-align: bottom; // important
  >* { float: left; &:not(:first-child) { @extend .left-distance; }}
}
.split-group { &>*:not(:last-child) { @include border-bottom($line-light); }; }
.total-wrap { 
	float: right;
	@extend .clear;
	display: inline-block;
	& >* { float: left; }
	.total-current { color: $warning;}
	line-height: 40px; 
	.total-description { @extend .secondary; &+* { margin-left: 10px; }}
	.total-price { color: $warning; font-size: 15px; margin-left: 2px;}
	&.subtotal { .total-price { font-size: 13px; } line-height: 20px; }
	&.grand-total { .total-price { font-size: 18px; font-weight: bold;} }
	.total-amount:before { content: '×'; padding: 0 2px; }
}

@mixin init-section($padding: 25px 40px, $marginTop: 20px) {
	padding: $padding;
	position: relative;
	@extend .clear;
	&>*:not(:first-child) { margin-top: $marginTop; }
	

}


.h-center { display: inline-block; position: absolute; left: 50%; transform: translatex(-50%); }
.v-center { display: inline-block; position: absolute; top: 50%; transform: translatey(-50%); }
.h-v-center { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%)}
.flex-h-v-center { display: flex; justify-content: center; align-items: center; }
.flex-h-center { display: flex; justify-content: center; }
.flex-v-center { display: flex;  align-items: center; }
.space-between-center { display: flex; justify-content: space-between; align-items: center;}














