@import './reset';
@import './variables';
@import './iconfont/icons';
@import './schemes';
@import './component';
@import './elements/form';
@import './vue-animation.scss';

body {
	
	font: 13px/1.5 "Microsoft Yahei", "Helvetica Neue", Helvetica, Arial, "Hiragino Sans GB", "Heiti SC", "WenQuanYi Micro Hei", sans-serif;
	color: $font-dark;
	background: #eee;
	-webkit-font-smoothing: antialiased;
	line-height: $unit*2;
	min-width: 1200px;
	text-rendering: optimizeLegibility;
	text-align: left;
}
svg {
	width: 20px;
	height: 20px;
	-webkit-tap-highlight-color:transparent;
	fill: $dark;
	stroke: $light;
	stroke-width: 0.07rem;
	vertical-align: middle;
	overflow: hidden;
	stroke-linecap: inherit;
	&.svg-stroke { fill: none; }
	&.svg-fill { stroke: none; }
}



