
.page { 
  @extend .clear;
  position: relative;
  width: 1040px; 
  margin: 0 auto;
  background: #fff;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.26); 
  @extend .split-group;
  .page-section { @include init-section(); }
}

.couple-group .couple:not(:first-child) { margin-top: 10px; }
.static-couple-group .couple { &:not(:first-child) { margin-top: 15px; } .couple-title { @extend .unwraped; } }

.couple {
  width: 100%;
  position: relative;
  @extend .clear;
  .couple-title { 
    position: relative; 
    float: left; 
    width: 110px;
    color: $font-light; 
    @extend .wrap-14;
  }
  .couple-content { 
    word-break: break-all;
    float: left;
    margin-left: 10px;
    .u-input { width: 460px; } 
  }
}


.itemlist {
  position: relative;
  width: 100%;
  min-width: 200px; 
  max-width: 600px;
  padding: 8px;
  border-radius: 4px;
  color: $font-dark;
  z-index: 1;
  background: #fff;
  box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.25); 
  >li {
    position: relative;
    padding: 5px 10px;
    cursor: pointer;
    &:not(:first-child) { margin-top: 10px; }
    &:hover { background: $feather; color: $primary; z-index: 2}
    &.active { color: $warning; }
  }
  &.fill-gray {

    padding: 6px 0;
    background: $feather;
    box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.25); 
    >li {
      padding: 8px 10px;
      &:hover { background: #fff; color: $primary; z-index: 2}
    }
    
  }
}



.tablelist { 
  width: 100%; 
  position: relative;
  @extend .clear;
  span { word-break: break-all; }

  .header-row, .main-row {
    position: relative;
    width: 100%;
    @extend .clear;
    >li { float: left; padding: 8px; }
  }
  .main-row.multiple-row >li:not(.oplist) { 
    >*:nth-child(2) { margin-top: 4px; }
    >*:not(:first-child) { color: $font-light; }
  }

  .header-row { @extend .secondary; }
  &:not(.tablelist-basic) .main-row { 
    background: inherit;
    @extend .zebra-stripe;
    // @include border-top();
    &:hover { @include inset-shadow-border($strokeWidth: 2px); }
  }

  &.tablelist-basic {
    .main-row>li, .header-row>li{ padding: 5px 0;}
  }
}


.formlist {
  position: relative;
  width: 100%;
  @include border-top();
  @include border-right();
  &:not(:nth-of-type(1)) { @extend .top-distance; }
  .colspan1 { width: 33.333% !important; }
  .colspan2 { width: 66.666% !important; }
  .colspan3 { width: 100% !important; }
  .row {
    width: 100%;
    position: relative;
    height: auto;
    @extend .clear;
    >li { 
      float: left;
      width: 33.333%; 
      min-height: 41px;
      @include border-left-bottom; 
      @extend .clear;
      .couple{
        padding-left: 10px;
        padding-right: 10px;
        .couple-title,
        .couple-content {
          width: auto;
          @extend .wrap-20;
        }
      }
      // add-fzy 
      .checkbox-group,
      .radio-group {
        padding-left: 10px;
        padding-right: 10px;
        width: auto;
        @extend .wrap-20;
      }
        @at-root .multiple li {
        padding: $unit;
      }

      .u-input { input { padding-top: 9px; padding-bottom: 9px; border-color: #fff; } }
      &.active .u-input { z-index: 10; }
      .select-ctr { width: 100%; }
    }
  }
}


.tool-bar { 
  height: 30px;
  position: relative; 
  &>* { @extend .left-side; }
  &>*.right-side { @extend .right-side; }
  .right-side:nth-of-type(1) { margin-right: 0;}
  .text-button { @extend .coat-10;}
  .couple { width: auto; margin: 0 !important;
    .couple-title { width: auto; @extend .wrap-10;}
  }
}


.filter-bar {
  position: relative;
  width: 100%;
  @extend .clear;
  &>* { float: left; }
  .filter-list {
    max-width: calc(100% - 150px);
    .u-input { width: 260px; input { padding-top: 4px; padding-bottom: 4px; }}
    &>* { 
      margin-right: 10px;
      &.collapsible { margin-top: 10px; display: none; &.visible { display: block; } }
    }
  }
  .filter-btn-list li { cursor: pointer; }
}



.u-title {
  @extend .clear;
  @extend .wrap-10;
  &.u-title-max { .title-text { font-size: 30px; line-height: 30px; } &+* { margin-top: 30px; }}
  &.u-title-large { .title-text { font-size: 18px;} &+* { margin-top: 20px; }}
  .title-text { display: inline-block; vertical-align: top; &+* { margin-left: $unit; } }

}


.popover-up-arrow, .popover-down-arrow {
  position: absolute;
  z-index: 1002;
  &.popover-up-arrow { 
    top: 100%; 
    margin-top: 10px;
    &:before { top: -6px; box-shadow: -3px -3px 8px rgba(#000, 0.06); }
    &:after { top: 0; }
  }
  &.popover-down-arrow { 
    margin-bottom: 10px; 
    bottom: 100%;
    &:after { bottom: 0; }
    &:before { margin-top: 4px;  bottom: -6px; box-shadow: 3px 3px 8px rgba(#000, 0.06); } 
  }
  &:after {
    content: ' ';
    width: 100%;
    height: 10px;
    left: 0;
    position: absolute;
    border-radius: 4px; 
    background: inherit;
  }
  
  &:before {
    content: ' '; 
    width: 15px; 
    height: 15px; 
    background: inherit;
    position: absolute; 
    transform-origin: center;
    transform: translatex(-50%) rotate(45deg);
    @extend .h-center; 
  }
}
.popover-down { top: 100%; margin-top: 3px; }

.popover {
  position: relative;
  display: inline-block;
  text-align: left;
  z-index: 10001;

  .popover-content-container { 
    position: absolute;
    z-index: 1002; 
    width: 100%; 
    bottom: 0;
    height: 100%;
    .popover-content { 
      position: absolute;
      background: #fff; 
      padding: 10px; 
      min-width: 320px; 
      width: auto; 
      min-height: 60px;
      max-width: 400px;
      border-radius: 4px; 
      transform-origin: center;
      box-shadow: 0 2px 16px 0 rgba(0, 0, 0, 0.3);
      @extend .h-center;
    }
  }
  .itemlist { 
    width: auto; 
    @extend .h-center;
  }
}


.tree-menu {
  padding: 20px 0;
  a { transition: all .25s; display: inline-block; width: 100%; }
  a:not(.icon-downTriangle):hover, a:not(.icon-downTriangle).on { 
    color: black;
    transform: translate(5px, 0);
  }
  width: 140px; 
  .first-menu {

    &:not(:first-child) { margin-top: 10px; }
    position: relative;
    cursor: pointer;
    >a {
      padding: 5px 30px;
      box-sizing: border-box;
      display: block;
    }
    .icon-downTriangle {
      position: relative;

      &:before { 
        font-size: 8px; 
        padding-right: 2px; 
        color: $font-light; 
        vertical-align: 2px;
        position: absolute; 
        left: 20px;
        top: 9px;
        opacity: 0.6;
      }
    }
    .second-menu-wrap {
      .second-menu {
        position: relative;
        padding: 5px 30px;
        color: $font-light;
      }
    }
  }
}



.modal {
  width: 100%; 
  height: 100%; 
  position: fixed; 
  background: rgba(#000, 0.65); 
  top: 0; 
  left: 0; 
  z-index: 10001;
  &.s {
    .modal-card { width: 540px; margin-top: -100px; }
  }
  .title-text { font-size: 16px; }
  .close-btn {
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer; 
    color: $font-light;
    .icon-delete { font-size: 18px; @include shadow-circle-icon(18px); }
    z-index: 10002;
  }

  .modal-card {
    @extend .h-v-center;
    background: #fff;
    width: 640px;
    max-height: calc(100% - 30px);
    overflow: auto;
    border-radius: 8px;
    .section {
      padding: 15px 30px;
      .u-input { width: 100%; }

      &.body { min-height: 280px; }

      &.footer {
        height: auto;
        @extend .clear;
        padding: 15px 20px;
        .cancel-btn { margin-right: 20px; }
      }
    }
  }
}


.detail-list {
  @extend .split-group; 
  background: $feather;
  .detail-section { @include init-section($padding: 10px 15px); @extend .split-group; } 
}

.text-button { 
  color: $primary; 
  cursor: pointer; 
  white-space: nowrap;
  &:hover { text-decoration: underline;} 
}

.toggle-box {.content { margin-top: 20px; }}

.num-spinner {
  width: 80px;
  @extend .clear;
  &>* { float: left; }
  background: #fff;
  input { width: 50px; text-align: center; }
  .add-btn { width: 15px; cursor: pointer; }
  .minus-btn { width: 15px; cursor: pointer; }
}

.button {
  position: relative;
  padding: 5px 10px;
  min-width: 60px;
  border-radius: 4px;
  user-select: none;
  text-align: center;
  cursor: pointer;
  transition: all 0.2s ease;
  display: inline-block;
  &.button-small { min-width: 20px; padding: 2px 10px; border-radius: 3px; }
}


.omni-button {
  .icon-downArrow:before { position: absolute; right: 4px; }
  .itemlist { min-width: 120px;}
}



.tab-bar {
  width: 100%;
  @extend .flex-v-center; 
  text-align: center;
  position: relative;
  .accessary { margin-left: 50px; @extend .flex-v-center;}
  @extend .clear;
  .tab-list {
   @extend .flex-h-v-center;
   >* {
    padding: $unit 15px;
    position: relative;
    float: left;
    cursor: pointer;
  }
} 
}

.card-bar {
  background: $feather;
  .tab-list>* {
    margin-bottom: -1px;
    min-width: 100px;
    @include border-right($line-light);
    &:before {
      content: '';
      width: 100%;
      height: 2px;
      position: absolute;
      top: -1px;
      left: 0;
      background: primary;
    }
    &.active {
      background: #fff;
      color: $primary;
      &:before { background: currentColor; }

    }
  }
}

.slider-bar .tab-list>* { 
  &.active { color: $primary; } 
  &.slider { 
    height: 4px; 
    width: 0;
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 0;
    background: $primary;
  }
}


.segment-bar .tab-list>* {
  padding-top: 4px;
  padding-bottom: 4px;
  @include border($primary);  
  color: $primary;
  background: #fff;
  &:not(:first-child) { margin-left: -1px; }
  &:first-child { border-radius: 3px 0 0 3px; }
  &:last-child { border-radius: 0 3px 3px 0; }
  &.active { 
    background: $primary; 
    color: #fff; 
    border-color: $primary; 
    index: 1;
    &+* { @include border-left($primary);}
  }
}




.timeline {
  margin-left: 10px;
  &.packed { .hidden { display: none; } }
  .line-item {
    &.showmore { position:relative; top: -16px; display: inline-block; }
    position: relative;
    &.line-item:not(:nth-last-of-type(2)) {padding-bottom: 40px;}
    @include border-left(); 
    padding-left: 20px;
    .icon-hook {
      background: #fff;
      position: absolute;
      left: -11px;
      width: 20px;
      height: 20px;
      @include border($strokeWidth: 1.5px);
      color: #fff;
      border-radius: 50%;
      &:before {
        @extend .h-v-center;
      }
    }
    &:last-child { border-color: transparent; }
    &.new { border-color: $green; .icon-hook { border-color: inherit; background: $green; }}
    &.on {
      border-color: $green;
      .icon-hook {
        border-color: inherit;
        color: $green;
      }
    }
  }
}

.double-input {
  position: relative;
  display: inline-block;
  float: left;
  width: 260px;
  padding: 6px 8px;
  @extend .clear;
  border-radius: 3px;
  @include border();
  background: #fff;
  transition: border-color .3s;
  &:hover {  border-color: $font-dark; input::-webkit-input-placeholder { opacity: .6; } }
  .u-input { padding: 0; width: 30%; border: none; }
  .ledge { padding: 0 4px; }
  &>* { float: left; }
}

.more-button{
  position: relative;
  width: 100%;
  height: 100%;
  @extend .flex-h-v-center;
  >ul {
    height: 20px;
    position: relative;
    width: 20px;
    @extend .space-between-center;
    >li {
      width: 4.5px;
      height: 4.5px;
      border-radius: 50%;
      flex-shrink: 0;
      background-color: currentColor;
      @extend .flex-h-v-center;
      &.on:not(.close) { background-color: $primary !important; }
      &:nth-child(n+4) { position: absolute; right: 0; }

      svg {
        fill: none;
        color: $light;
        &.on {
          fill: $primary; 
          stroke: $primary;
          &.i-thumbup { color: #fff; }
        }
      }

      >* {    
        width: auto;
        color: currentColor;
        flex-shrink: 0;
        text-align: center;
        cursor: pointer;
      }
    }
  }
}

.animated-icon-container {
 width: 20px;
 height: 20px;
 svg { @extend .h-v-center; width: 16px; height: 16px;}
 .animated-icon-cross { stroke-width: 3px !important; path { opacity: 0; } };
}

.editable-list-pro {
  .add-button { margin-top: 10px; display: inline-block; }
}


.editable-list {

  .editable-item{ 
    &.active  { 
      @extend  .light-shadow; 
      background: #fff; 
      z-index: 1000; 
      position: relative;
    }
    &:not(:nth-of-type(1)) {
      margin-top: 5px; 
    }
  }
  >* { 
    position: relative;
    
    .delete-button-container {
      position: absolute; 
      right: 0px; 
      top: 0px;
      width: 30px; 
      height: calc(100% - 2px);
      margin: 1px 1px 0 0;
      z-index: 10;
      overflow: hidden;
      .delete-button {
        width: 100%;
        height: 100%;
        background: $feather;
        @include border-left();
        .icon-trash {
          cursor: pointer;
          display: inline-block;
          padding: 9px 6px;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          color: red;
          &:before { font-size: 16px; color: $font-light; transition: all .3s; }
          &:hover { &:before { color: $font-dark; } }
        } 
      }
    }
  }
}

.paddle-nav { 
  width: calc(1040px + 50px); 
  left: calc(220px + (100% - 1260px)/2 - 25px);
  // position: fixed; 
  height: 50px; 
  top: 50%;
  z-index: 1000;
  box-sizing: border-box; 
  pointer-events: none;
  .paddle-nav-arrow { 
    position: relative; 
    width: 50px; 
    height: 50px; 
    border-radius: 50%; 
    text-align: center; 
    line-height: 50px; 
    background: rgba(0, 0, 0, 0.1); 
    color:#fff; z-index: 10; 
    transition: all .25s; 
    position: absolute; 
    -webkit-user-select: none; 
    pointer-events: auto;
    &.hidden { display: none };
    &:hover { background: #ff6000 }
    &.paddle-nav-arrow-previous { left: 0; }
    &.paddle-nav-arrow-next { right: 0; }
  }
  
}




