
body{font-family:PingFang SC,Lantinghei SC,Helvetica Neue,Helvetica,Arial,Microsoft YaHei,\\5FAE\8F6F\96C5\9ED1,STHeitiSC-Light,simsun,\\5B8B\4F53,WenQuanYi Zen Hei,WenQuanYi Micro Hei,'sans-serif';-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#717171
}
#app,body,html{height:100%
}
.markdown{padding-left:30px
}
.markdown h1{font-size:28px;font-weight:500;line-height:40px;margin-bottom:24px;margin-top:8px
}
.markdown h2,.markdown h3,.markdown h4,.markdown h5,.markdown h6{margin:.3em 0 .6em;font-weight:500;clear:both
}
.markdown h2{font-size:22px
}
.markdown h3{font-size:18px
}
.markdown h4{font-size:16px
}
.markdown h5{font-size:14px
}
.markdown section>ul li{margin-left:20px;padding-left:4px;list-style:circle;line-height:25px
}
.markdown section>p{margin-bottom:20px;line-height:24px
}
.markdown strong{font-weight:500
}
.markdown h2 a,.markdown h3 a,.markdown h4 a{font-size:.8em;opacity:0;transition:opacity .2s ease-in-out
}
.markdown h2:hover a,.markdown h3:hover a,.markdown h4:hover a{opacity:1
}
.markdown section>table{border-collapse:collapse;border-spacing:0px;empty-cells:show;border:1px solid #e9e9e9;width:100%;margin:16px 0
}
.markdown section>table th,.markdown section>table td{border:1px solid #e9e9e9;padding:8px 16px;text-align:left
}
.markdown section>table th{background:#F7F7F7
}
.list-group .menu{box-shadow:none
}
.header-anchor{margin-left:-18px
}
.side-nav{border-right:1px solid #e9e9e9
}
pre code{background-color:#f8f8f8
}
.footer{padding:40px 20px
}
.hljs{display:block;overflow-x:auto;padding:0.5em;background:#2b2b2b
}
.hljs{color:#bababa
}
.hljs-strong,.hljs-emphasis{color:#a8a8a2
}
.hljs-bullet,.hljs-quote,.hljs-link,.hljs-number,.hljs-regexp,.hljs-literal{color:#6896ba
}
.hljs-code,.hljs-selector-class{color:#a6e22e
}
.hljs-emphasis{font-style:italic
}
.hljs-keyword,.hljs-selector-tag,.hljs-section,.hljs-attribute,.hljs-name,.hljs-variable{color:#268bd2
}
.hljs-params{color:#b9b9b9
}
.hljs-attr{color:#B58900
}
.hljs-string{color:#2aa198
}
.hljs-subst,.hljs-type,.hljs-built_in,.hljs-builtin-name,.hljs-symbol,.hljs-selector-id,.hljs-selector-attr,.hljs-selector-pseudo,.hljs-template-tag,.hljs-template-variable,.hljs-addition{color:#e0c46c
}
.hljs-comment,.hljs-deletion,.hljs-meta{color:#7f7f7f
}



