
.logo-icon{margin-top:40px;margin-bottom:30px
}
.logo-icon img{max-width:150px
}
#app .is-brand img{max-height:30px
}
.list-group .list-group-item{position:relative;display:block;padding:5px 0px
}
.list-group .menu{margin-top:10px
}
.nav_box{width:100%;height:60px
}
.nav_box .menulst{max-width:1200px;display:block;margin:0 auto
}
.nav_box .menulst li{float:right;padding:0 20px;line-height:60px;cursor:pointer;transition:all .25s
}
.nav_box .menulst li.on,.nav_box .menulst li:hover{color:#7ed321
}
.nav_box .logo{float:left;display:inline-block;line-height:60px;color:#7ed321;font-size:30px;font-style:normal
}



