
.box-demo{padding:0;border:1px solid #e9e9e9;border-radius:4px;box-shadow:none
}
.box-demo-show{padding:20px 25px 30px;border-bottom:1px solid #e9e9e9
}
.box-demo-description{position:relative;padding:17px 16px 15px 20px;border-radius:0 0 6px 6px;transition:background-color 0.4s ease;width:100%;font-size:12px
}
.box-demo-description.bordered{border-bottom:1px dashed #e9e9e9
}
.box-demo-description h3,.box-demo-description h4{position:absolute;top:-10px;padding:1px 8px;margin-left:-8px;margin-top:0;margin-bottom:0;color:#777;border-radius:4px;border-top-left-radius:0;background:#fff;transition:background-color 0.4s ease
}
.box-demo-description h3 .header-anchor,.box-demo-description h4 .header-anchor{display:none
}
.box-demo-description li{line-height:21px
}
.box-demo-code{transition:height .2s ease-in-out;overflow:auto
}
.btn-toggle{position:absolute;right:16px;bottom:17px;cursor:pointer;width:18px;height:18px;font-size:18px;line-height:18px;color:#999
}
.btn-toggle i{transition:all 0.3s
}
.btn-toggle i:before{font-size:16px
}
.open i{transform:rotate(-180deg)
}



