//Button Schemes

$button-fill-primary: 'button-fill-primary' $primary #fff;
$button-fill-complement: 'button-fill-complement' $complement #fff;
$button-fill-dark: 'button-fill-dark' $dark #fff;
$button-fill-light: 'button-fill-light' $light $dark;
$button-fill-warning: 'button-fill-warning' $warning #fff;

$button-stroke-primary: 'button-stroke-primary' $primary;
$button-stroke-complement: 'button-stroke-complement' $complement;
$button-stroke-warning: 'button-stroke-warning' $warning;
$button-stroke-light: 'button-stroke-light' $dark $light $light;

$button-plain-primary: 'button-plain-primary' $primary;
$button-plain-warning: 'button-plain-warning' $warning;
$button-plain-complement: 'button-plain-complement' $complement;
$button-plain-dark: 'button-plain-dark' $dark $light $transparent;

$button-fill-schemes: $button-fill-primary $button-fill-complement $button-fill-dark $button-fill-warning $button-fill-light;
$button-stroke-schemes: $button-stroke-primary $button-stroke-complement $button-stroke-warning;
$button-plain-schemes: $button-plain-primary $button-plain-complement $button-plain-warning;
$button-dark-schemes: $button-plain-dark $button-stroke-light;


@each $scheme in $button-fill-schemes {
	.button.#{nth($scheme, 1)}  {
		background-color: nth($scheme, 2);
		color: nth($scheme, 3);
		&:hover { background: darken(nth($scheme, 2), 7%); } 
	}
}
@each $scheme in $button-stroke-schemes {
	.button.#{nth($scheme, 1)} {
		color: nth($scheme, 2);
		@include inset-shadow-border($color: nth($scheme, 2));
		&:hover { background: nth($scheme, 2); color: #fff; } 
	}
}
@each $scheme in $button-plain-schemes {
	.button.#{nth($scheme, 1)} {
		color: nth($scheme, 2);
		&:hover { background: nth($scheme, 2); color: #fff; } 
	}
}
@each $scheme in $button-dark-schemes {
	.button.#{nth($scheme, 1)} {
		color: nth($scheme, 2);
		@include inset-shadow-border($color: nth($scheme, 4));
		&:hover { background: nth($scheme, 3);}
	}
}


